.Accueil_empty_space {
  height: 50px;
}

.RetoursClients_container {
  /* background-color: sandybrown; */
  width: 80%;
  margin: auto;
  /* border: 1px solid black; */
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 4px;
}

.RetoursClients_part_title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.RetoursClients_avis_title {
  width: 54%;
}
.RetoursClients_photos_title {
  width: 44%;
}

.RetoursClients_separation {
  height: 2px;
  width: 100%;
  background-color: black;
  /* margin-top: 10px; */
  margin-bottom: 10px;
}

.RetoursClients_part_content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.RetoursClients_avis_container {
  width: 54%;
  background-color: rgb(206, 206, 206);
}

.RetoursClients_photos_container {
  width: 44%;
}

.RetoursClients_avis_container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.RetoursClients_avis_card {
  /* background-color: seagreen; */
  display: flex;
  flex-direction: row;
  height: 30%;
  width: 100%;
  padding: 5px;
}

.RetoursClients_avis_card_picture_container {
  height: 100%;
  width: 30%;
  /* background-color: skyblue; */
  display: flex;
  justify-content: center;
  align-items: center;
}

.RetoursClients_avis_card_picture_circle {
  /* height: 30%; */
  height: 100px;
  width: 100px;
  /* width: 30%; */
  /* width: 30%; */
  /* padding-bottom: 30%; */
  background-color: white;
  border-radius: 110px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.RetoursClients_avis_card_picture {
  max-width: 70%;
  max-height: 70%;
}

.RetoursClients_avis_card_text {
  /* background-color: steelblue; */
  width: 70%;
  height: 100%;
}

.accueil_page_suivez_nous_container {
  display: flex;
  /* width: 30%; */
  margin: auto;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  /* background-color: greenyellow; */
  font-size: 2em;
}

