.productCardContainer {
  /* background-color: rgb(255, 174, 0); */
  display: flex;
  flex-direction: column;
  color: black;
  text-decoration: none;
  margin-bottom: 5px;
  width: 100%;
  height: 0;
  padding-top: 110%;
  position: relative;
}

.productCardContainer_inside {
  /* background-color: rgb(0, 153, 255); */
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.productCardContainer:hover {
  color: black;
}

.productCardContainer_picture_wrap {
  /* background-color: red; */
  width: 100%;
  height: 78%;
}

.productCardContainer_picture {
  border-radius: 10px;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.productCardContainer_txt_container {
  /* background-color: green; */
  width: 100%;
  height: 22%;

  padding: 8px;

  /* background-color: transparent; */
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 50% 50%;
  grid-gap: 2px;

  grid-template-areas:
    "name price"
    "colors button";
}

.productCardContainer-txt-container div {
  /* background-color: green; */
}

.pCC_txt_name {
  grid-area: name;
  justify-self: start;
}

.pCC_txt_price {
  grid-area: price;
  justify-self: end;
}

.pCC_txt_colors {
  grid-area: colors;
}

.pCC_txt_button_wrap {
  grid-area: button;
  justify-self: end;
}

@keyframes clicked_btn {
  0% {
    transform: border
  }
}

.pCC_txt_button {
  /* color: white; */
  background-color: white;
  border: none;
  border: white 2px solid;
  /* border: black 1px solid; */
  border-radius: 5px;
}

.pCC_txt_button_clicked {
  /* color: white; */
  background-color: white;
  border: none;
  border: black 2px solid;
  border-radius: 5px;
}

.pCC_txt_button_txt {
  /* background-color: aqua; */
  padding: 0;
  margin: 0;
}

.pCC_txt_button_icc {
  /* background-color: aqua; */
  position: relative;
  top: 0px;
  left: 3px;
}