.Panier {
    background-color: white;
    z-index: 2;
}

.Panier_title {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.Panier_title h3 {
    /* text-align: center; */
    font-size: 1.7em;
    padding: 0 5%;
    padding-top: 10px;
}

.Panier_separator {
    height: 2px;
    width: 90%;
    background-color: black;
    margin: auto;
}

.Panier_item_wrap {
    /* background-color: rgb(36, 212, 36); */
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 90%;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 20px;
    /* padding: 0; */
}

.Panier_delete {
    flex: 40px;
    /* background-color: aqua; */
}

.Panier_item_name {
    flex: 45%;
    /* background-color: blueviolet; */
}

.Panier_item_name>h4 {
    padding: 0;
    margin: 0;
    /* background-color: yellow; */
}

.Panier_item_qty {
    flex: 15%;
    /* background-color: red; */
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin: 0;
    padding: 0;
}

.Panier_item_qty>h4, .Panier_item_qty>p {
    margin: 0;
    padding: 0;
}

.Panier_item_price>h4, .Panier_item_price>p {
    margin: 0;
    padding: 0;
}

.Panier_item_price {
    flex: 25%;
    /* background-color: green; */
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin: 0;
    padding: 0;
}

.Panier_somme_wrap {
    width: 90%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: auto;
    margin-top: 15px;
    /* background-color: aqua; */
    padding-left: 40px;
}

.Panier_somme_wrap h2 {
    /* background-color: aquamarine; */
    padding: 0;
    margin: 0;
}

.Panier_validation {
    text-align: center;
    padding-bottom: 20px;
    margin-top: 25px;
}

.Panier_validation_btn {
    text-decoration: none;
    padding: 8px;
    border: 1px solid black;
    border-radius: 8px;
    background-color: black;
    color: white;
    font-size: 1.5em;
}