@media (max-width: 510px) {
  .AccueilContainerCorps {
    width: 100%;
  }

  .AccueilContainerSub {
    width: 90%;
    /* background-color: aqua; */
    padding: 10px 5px;
    margin: auto;
    margin-top: 5px;
    border-top: 1px solid black;
  }

  .AccueilContainerSubTitle {
    /* background-color: burlywood; */
    text-align: center;
    margin: 20px 0;
  }

  .AccueilContainerCorps_picture {
    height: auto;
    max-width: 40vw;
  }

  .AccueilContainerSub_card_product {
    margin: 15px 5px;
    /* background-color: aquamarine; */
  }
}

