@media (max-width: 510px) {
    .sliderContainer2 {
        /* background-color: rgb(33, 211, 57); */
        height: 130vw;
        width: 100vw;
        overflow: hidden;
        margin: 0;
        padding: 0;
    }

    .slideBox2 {
        width: 100%;
        height: 120vw;
        display: inline-block;
        background-size: cover;
        background-position: center;
        background-color: white;
        /* background-color: red; */
    }

    .slideImageWrap2 {
        /* background-color: yellow; */
        height: 100%;
        width: 100%;
        /* object-fit: contain; */
    }

    .slideImage2 {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .slideTextLinkWrap2 {
        /* background-color: blueviolet; */
        /* border: 1px solid red; */
        height: 20%;
        margin-left: auto;
        margin-right: auto;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .slideTextLink {
        background-color: rgba(255, 255, 255, 0.8);
        /* border: solid 2px white; */
        /* border: solid 2px rgba(175, 102, 66, 1); */
        /* border: solid 1px black; */
        text-decoration: none;
        color: black;
        border-radius: 6px;
        padding: 8px;
        box-shadow: 5px 5px 5px black;

        display: flex;
        flex-direction: row;
        align-items: center;

        height: 70%;
    }

    .icc_txt {
        /* background-color: blueviolet; */
        padding: 0;
        margin: 0;
        font-size: 1.4em;
        /* color: red; */
    }

    .icc_btn {
        /* background-color: aqua; */
        padding-left: 8px;
        position: relative;
        top: 2px;
        font-size: 1.4em;
    }

    .btn_div22 {
        /* background-color: yellow; */
        height: 10vw;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        text-align: center;
    }

    .active_btn_head {
        /* background-color: black; */
        /* color: rgba(175, 102, 66, 1); */

        margin: 0 8px;
        padding: 1px;
        max-height: 60%;
        height: auto;
        /* background-color: aqua; */
    }

    .btn_head {
        margin: 0 8px;
        padding: 1px;
        max-height: 60%;
        height: auto;
        /* background-color: orange; */
    }

    .btnPaw {
        color: white;
        margin: 0 10px;
        padding: 1px;
        background-color: rgba(0, 0, 0, 0.856);
        border-radius: 5px;
        font-size: 1.5em;
    }

    .active_btnPaw {
        /* background-color: rgba(176, 51, 214, 0.856); */
        background-color: white;
        /* color: rgba(175, 102, 66, 1); */

        /* background-color: rgba(175, 102, 66, 1); */
        color: black;
    }

    .btn_circle {
        color: black;
        margin: 0 10px;
        padding: 1px;
        background-color: rgba(0, 0, 0, 0.856);
        background-color: rgba(0, 0, 0, 0.856);
        background-color: white;
        border-radius: 10px;
        font-size: 1.5em;
        width: 14px;
        height: 14px;
        border: 2px solid black;
    }

    .active_btn_circle {
        /* background-color: rgba(176, 51, 214, 0.856); */
        background-color: black;
        /* color: rgba(175, 102, 66, 1); */

        /* background-color: rgba(175, 102, 66, 1); */
        color: black;
        width: 14px;
        height: 14px;
        border: 2px solid black;
        border-radius: 10px;
    }

}

