.HeaderMail {
  background-color: rgb(221, 214, 187);
  display: flex;
  justify-content: center;
  align-items: center;
  /* text-align: center; */
  /* padding: 3px; */
  height: 23px;
  /* background-color: tomato; */
}
.HeaderMail p{
  padding: 0px;
  margin:0px;
  /* background-color: tomato; */
}

@media (min-width: 510px) {
  .Header_desktop_main_wrap {
    box-shadow: 0px 2px 10px black;
    position: fixed;
    z-index: 10;
    background-color: white;
    width: 100vw;
  }

  .Header_grid_desktop {
    width: min(100%, 1450px);
    margin: auto;
    background-color: transparent;
    display: grid;
    grid-template-columns: repeat(3, 1fr) 1.4fr repeat(3, 1fr);
    grid-template-rows: 60px 05px 55px;
    grid-gap: 0px;

    grid-template-areas:
      "fb fb fb logo cart cart cart"
      ". . . logo . . ."
      "nav nav nav logo nav2 nav2 nav2"
  }

  .Header_reseaux_part {
    grid-area: fb;
  }

  .Header_cart_part {
    grid-area: cart;
  }

  .H_part {
    /* background-color: purple; */
    /* text-align: end; */
    display: flex;
    justify-content: flex-start;
    padding: 0 0 0 40px;
  }

  .H_part_right {
    justify-content: flex-end;
    padding: 0 40px 0 0;
  }

  .H_part a {
    /* background-color: palegoldenrod; */
    color: black;
    font-size: 2.5em;
    /* width: 100%; */
    margin: 0 0.2vw;
    display: flex;
    align-items: center;
  }

  .Header_cart_icc_wrap {
    background-color: white;
    border-radius: 25px;
    height: 25px;
    width: 25px;
    border: solid black 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* border: solid red 2px; */
    position: absolute;
    z-index: 10;
  }

  .Header_cart_icc_wrap_white {
    background-color: transparent;
    /* background-color: rgba(255, 166, 0, 0.589); */
    border-radius: 250px;
    height: 30px;
    width: 30px;
    border: solid white 12px;
    /* border: solid rgb(235, 20, 20) 12px; */
    display: flex;
    justify-content: center;
    align-items: center;
    /* border: solid red 2px; */
    position: relative;
    left: -7px;
    top: 0px;
    z-index: 10;
  }

  .Header_cart_icc {
    color: black;
    /* color: purple; */
    font-size: 0.8em;
    ;
  }

  .Header_navLink_left {
    /* background-color: rgb(178, 178, 238); */
    grid-area: nav;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 2px;
    /* align-items: center; */
    /* justify-items: center; */
  }

  .Header_navLink_right {
    /* background-color: rgb(178, 178, 238); */
    grid-area: nav2;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 2px;
  }

  .Header_navLink_child {
    /* background-color: orange; */
    text-decoration: none;
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: calc(0.45em + 1vw);
    /* border-radius: 10px; */
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    /* border: 2px solid white; */

  }

  .Header_navLink_child2 {
    /* box-sizing: border-box; */
    /* width: 200px; */
    /* height: 200px; */
    border: 10px solid blue;
    background: green;
    box-shadow: 0px 0px 0px 10px red inset;
  }

  .Header_navLink_child:hover {
    color: brown;
    /* background-color: grey; */
    /* border: 2px solid grey; */
    /* color: white; */

    /* box-shadow:
      0px 0px 0px 3px white inset, */
    /* 0px 0px 0px 5px red ; */

  }

  .Hnc_active {
    color: brown;
  }

  .Header_logo_wrap {
    /* background-color: rgb(0, 238, 255); */
    grid-area: logo;
    /* justify-content: center;
  text-align: center;
  vertical-align: middle; */
    display: flex;
    justify-content: center;
    align-items: center;
    width: auto;
    /* height: 100%; */
  }

  .Header_logo {
    /* background-color: palevioletred; */
    /* font-size: 9em; */
    max-width: 100%;
    max-height: 90%;
  }

  .Header_ligne {
    /* grid-area: ligne; */
    /* background-color: palegreen; */
    border-top: 2px solid black;
    border-bottom: 3px solid black;
  }

  .Header_ligne_left {
    grid-area: ligne;
  }

  .Header_ligne_right {
    grid-area: ligne2;
  }

  .NavBar_space {
    height: 120px;
  }


}

@media (max-width: 510px) {
  .Header_grid_phone {
    width: 100%;
    background-color: white;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 60px 2px 5px;
    grid-gap: 0px;

    grid-template-areas:
      "menu logo cart"
      ". . ."
      "ligne ligne ligne";
    /* border: solid 5px red; */

    position: fixed;
    z-index: 10;
  }

  .NavBar_space {
    height: 65px;
  }

  .Header_cart_part {
    /* background-color: purple; */
    grid-area: cart;
    text-align: end;
    display: flex;
    /* justify-content: end; */
    justify-content: flex-end;
    align-items: center;

    /* color: black; */
  }

  .Header_cart_part button {
    /* background-color: rgb(230, 149, 27); */
    /* color: black; */
    /* font-size: 2.3em; */
    /* width: 100%; */
    margin: 0 26px;
    border: none;
    color: black;
    /* background-color: white; */
    display: flex;
    flex-direction: row;
    align-items: center;
    text-align: center;
    background-color: white;
    /* position: absolute; */
  }

  .Header_cart_icc {
    /* background-color: cadetblue; */
    /* transform: rotate(45deg); */
    /* border-radius: 25px; */
    /* width: 20px; */
    font-size: 1.5em;
  }

  .Header_cart_number {
    /* background-color: aqua; */
    width: 0;
    font-size: 0.7em;
    color: white;
    position: relative;
    top: -3px;
    left: -11px;
    /* display: inline-block; */
  }

  .Header_menu_part {
    /* background-color: purple; */
    grid-area: menu;
    text-align: start;
    display: flex;
    /* justify-content: end; */
    justify-content: flex-start;
    align-items: center;
    color: black;
  }

  .Header_menu_part button {
    /* background-color: palegoldenrod; */
    /* color: black; */
    /* font-size: 2.3em; */
    /* width: 100%; */
    margin: 0 26px;
    border: none;
    color: black;
    background-color: white;
  }

  .Header_menu_icc {
    font-size: 1.5em;
  }

  .Header_logo_wrap {
    /* background-color: rgb(0, 238, 255); */
    grid-area: logo;
    /* justify-content: center;
  text-align: center;
  vertical-align: middle; */
    display: flex;
    /* justify-content: start; */
    justify-content: center;
    align-items: center;
  }

  .Header_logo {
    /* background-color: palevioletred; */
    /* font-size: 1em; */
    max-height: 100%;
    width: auto;
  }

  .Header_ligne {
    grid-area: ligne;
    /* background-color: palegreen; */
    /* border-top: 1px solid black; */
    border-bottom: 2px solid black;
  }

  .backdrop {
    position: fixed;
    top: 65px;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    z-index: 1;
  }

  .Header_phone_menu {
    background-color: white;
    flex-direction: column;
    list-style: none;
    padding-left: 0;
    z-index: 2;
    margin: 0;
  }

  .Header_phone_menu li {
    /* background-color: violet; */
    display: flex;
    justify-content: center;
    margin: 0px 0;
  }

  .Header_phone_menu li a {
    text-decoration: none;
    /* background-color: aquamarine; */
    color: black;
    border-bottom: 1px solid black;
    font-size: 1.7em;
    width: 80%;
    text-align: center;
    padding: 10px 0;
  }

  .Header_phone_menu li a.nop {
    border-bottom: none;
  }

  .Cart_phone_menu {
    background-color: white;
    z-index: 2;
  }

  .Cart_phone_menu_title {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .Cart_phone_menu_title h3 {
    /* text-align: center; */
    font-size: 1.7em;
    padding: 0 5%;
    padding-top: 10px;
  }

  .Cart_phone_menu_close {
    /* background-color: rgb(255, 127, 127); */
  }

  .Cart_phone_menu_separator {
    height: 2px;
    width: 90%;
    background-color: black;
    margin: auto;
  }

  .Cart_phone_menu_item_wrap {
    /* background-color: rgb(36, 212, 36); */
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 90%;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 20px;
    /* padding: 0; */
  }

  .Cart_phone_menu_delete {
    flex: 15%;
    /* background-color: aqua; */
  }

  .Cart_phone_menu_item_name {
    flex: 45%;
    /* background-color: blueviolet; */
  }

  .Cart_phone_menu_item_name>h4 {
    padding: 0;
    margin: 0;
    /* background-color: yellow; */
  }

  .Cart_phone_menu_item_qty {
    flex: 15%;
    /* background-color: red; */
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin: 0;
    padding: 0;
  }

  .Cart_phone_menu_item_qty>h4, .Cart_phone_menu_item_qty>p {
    margin: 0;
    padding: 0;
  }

  .Cart_phone_menu_item_price>h4, .Cart_phone_menu_item_price>p {
    margin: 0;
    padding: 0;
  }

  .Cart_phone_menu_item_price {
    flex: 25%;
    /* background-color: green; */
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin: 0;
    padding: 0;
  }

  .Cart_phone_menu_somme_wrap {
    width: 90%;
    display: flex;
    justify-content: space-between;
    margin: auto;
    margin-top: 5px;
  }

  .Cart_phone_menu_validation {
    text-align: center;
    padding-bottom: 20px;
    margin-top: 25px;
  }

  .Cart_phone_menu_validation_btn {
    text-decoration: none;
    padding: 8px;
    border: 1px solid black;
    border-radius: 8px;
    background-color: black;
    color: white
  }

}