.ArbresModels_title {
  /* background-color: lightcoral; */
  text-align: center;
  margin: 30px;
}

.ArbresModels_galerie {
  width: 80%;
  margin: auto;
  margin-bottom: 40px;
  /* background-color: lightgreen; */
  /* display: flex;
  flex-direction: row;
  justify-content: space-between; */
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 5% 5%;
}

.ArbresModels_galerie_pic_wrap {
  /* max-width: 100%; */
  /* height: auto; */
  /* background-color: mediumblue; */
  /* padding: 10%; */

  overflow: hidden;
  height: 0;
  padding: 60% 0;
  /* padding controls height, will always be perfectly square regardless of width */
  position: relative;
  opacity: 0.8;
  border: 1px solid black;
  cursor: pointer;
}

.ArbresModels_galerie_pic {
  /* max-width: 100%;
  height: auto; */
  /* background-color: yellow; */

  min-width: 100%;
  /* min-height: 100%; */
  height: auto;
  /* max-height: 100%; */
  max-width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  object-fit: contain;
}

