@media (min-width: 510px) {
  .FeedAccueil_container {
    /* background-color: burlywood; */
    width: 80%;
    margin: auto;
    border: 1px solid black;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding: 4px;
  }

  .FeedAccueil_button {
    width: 19%;
    display: flex;
    align-items: center;
    justify-content: center;
    /* vertical-align: bottom; */
    /* background-color: cadetblue; */
    cursor: pointer;
  }
  .FeedAccueil_arrow {
    /* width: 30%; */
    /* padding-bottom: 30%; */
    width: 50px;
    height: 50px;
    text-align: center;
    vertical-align: bottom;
    /* background-color: red; */
    border-bottom: 5px black solid;
    border-left: 5px black solid;
  }
  .arrow_left {
    transform: rotate(45deg);
  }
  .arrow_right {
    transform: rotate(225deg);
  }

  .FeedAccueil_picture_container {
    width: 19%;
    /* background-color: chartreuse; */
    height: auto;
  }

  .FeedAccueil_picture {
    width: 100%;
    height: auto;
  }
}
