@media (max-width: 510px) {

    .Creations_filter_wrap {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
    }

    .Creations_filter_liste {
        list-style-type: none;
        display: flex;
        flex-direction: column;
    }

    .Creations_filter_liste a {
        text-decoration: none;
        color: black
    }

    .filtre_creations_wrap {
        /* background-color: aqua; */
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }


}

@media (min-width: 510px) {
    .CC_filtre_wrap {
        /* background-color: aqua; */
        width: 80%;
        margin: auto;
    }

    .filtre_creations_wrap {
        /* background-color: aqua; */
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin: 15px 0;
    }

    .filtre_creations_titre {
        cursor: pointer;
        padding: 0;
        margin: 0;
    }

    .filtre_creations_select {
        /* background-color: orange; */
        /* height: 2em; */
        border: 2px solid black;
        border-radius: 5px;
    }

    .Creations_filter_wrap {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
    }

    .Creations_filter_liste {
        list-style-type: none;
        display: flex;
        flex-direction: column;
    }

    .Creations_filter_link_active {
        text-decoration: none;
        color: black;
        text-decoration: underline;
        
    }

    .Creations_filter_link {
        text-decoration: none;
        color: black;
    }




}