@media (max-width: 510px) {

    .creation_top_link {
        /* color: black; */
        display: flex;
        justify-content: space-around;
        margin-left: min(20%, 20px);
        margin-top: 10px;
        width: 60%;
        max-width: 220px;
    }

    .creation_top_link a, .creation_top_link p {
        /* color: black; */
        text-decoration: none;
    }

    .creation_picture_wrap {
        /* background-color: red; */
        width: 100%;
        height: 70%;
    }

    .creation_picture {
        border-radius: 10px;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .creation_titre_wrap {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: baseline;
        width: 80%;
        margin: 20px auto 5px auto;
    }

    .creation_titre_wrap h2, .creation_titre_wrap h3, .creation_titre_wrap h4 {
        margin: 0;
        /* vertical-align: middle; */
    }

    .creation_titre_wrap h2 {
        font-size: 2.2em;
    }

    .creation_titre_retours_wrap {
        display: flex;
        flex-direction: row;
        /* justify-content: space-between; */
        width: 80%;
        margin: 0px auto 10px auto;

    }

    .creation_titre_retours_icc {
        color: rgba(0, 0, 0, 0.801);
        font-size: 1.2em;
        margin: 0 2px;
    }

    .creation_titre_retours_icc_grey {
        color: grey;
        font-size: 1.2em;
        margin: 0 2px;
    }

    .creation_titre_retours_ICC_wrap {
        /* background-color: bisque; */
        display: flex;
        align-items: center;
    }

    .creation_titre_retours_ICC_txt {
        padding: 0;
        margin: 0;
    }

    .creation_btn_wrap {
        display: flex;
        margin: 10px auto 20px auto;
    }

    .creation_btn_txt {
        /* background-color: aqua; */
        padding: 0;
        margin: 0;
    }

    .creation_btn_icc {
        /* background-color: aqua; */
        position: relative;
        top: 3px;
        left: 12px;
    }

    .creation_accordeon_content {
        /* background-color: aquamarine; */
        width: 80%;
        /* margin: auto; */
        margin: 10px auto;
        padding: 0;
    }

    .creation_accordeon_content>p {
        margin: 0;
        /* background-color: bisque; */
    }



}

