@media (min-width: 510px) {

  .sliderContainerRC {
    /* background-color: rgb(33, 211, 57); */
    height: auto;
    width: 100%;
    overflow: hidden;
    margin: 0 0 15px 0;
    padding: 0px;
  }

  .slideBoxRC {
    width: 33%;
    height: auto;
    display: inline-block;
    background-size: cover;
    background-position: center;
    background-color: white;
    /* background-color: red; */
  }

  .slideTextLinkWrapRC {
    display: flex;
    flex-direction: column;
    background-color: rgb(212, 212, 212);
    /* background-color: green; */
    border-radius: 10px;
    margin: auto;
    padding: 10px;
    width: min(85%, 320px);
  }

  .slideTextTopRC {
    display: flex;
    /* background-color: cornflowerblue; */
    justify-content: space-between;
  }

  .txtRC {
    margin: 0;
    /* padding: 0; */
    /* background-color: crimson; */
    display: flex;
    padding: 5px 0;
    align-items: center;
    font-size: 1.1em;
  }

  .testICC {
    margin: 0 3px;
    /* background-color: yellow; */
    font-size: 1.2em;
    /* color: green; */
  }

  .testICC_grey {
    margin: 0 3px;
    /* background-color: yellow; */
    font-size: 1.2em;
    color: rgb(161, 161, 161);
  }

  .slideTextLineRC {
    height: 2px;
    background-color: white;
    width: 100%;
  }

  .slideTexttxt {
    white-space: pre-line;
  }

  .RC_btn_wrap {
    /* background-color: aqua; */
    height: 0;
    height: 5px;
    width: 80%;
    margin: auto;
    display: flex;
    justify-content: space-between;
  }

  .RC_btn {
    background-color: white;
    /* background-color: orange; */
    border: none;
    font-size: 2.4em;
    height: 50px;
    position: relative;
    top: -75px;
    border-radius: 10px;
    display: flex;
    align-items: flex-end;
    z-index: 1;
  }

  .RC_btn:hover {
    background-color: black;
    color: white;
  }

  .filtreTransparent {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    /* margin: auto; */
    /* background-color: aqua; */
    height: 0;
    height: 5px;
  }

  .filtreTransparent_left {
    /* border-left: 10px solid white; */
    background: linear-gradient(0.25turn,
        rgba(255, 255, 255, 1),
        rgba(255, 255, 255, 1) 25%,
        rgba(255, 255, 255, 1) 60%,
        rgba(255, 255, 255, 0.7) 85%,
        transparent
        /* rgba(255, 255, 255, 0.1) 75%, */
        /* rgba(255, 255, 255, 0.5) 85%, */
        /* rgba(255, 255, 255, 1) */
      );
    /* background: red; */
    height: 150px;
    width: 25%;
    position: relative;
    top: -150px;
    left: 0;
  }

  .inverse_filtre {
    transform: rotate(180deg)
  }

}